/* eslint-disable */

import { Validator } from 'vee-validate'

const dictionary = {
  name: 'ru',
  messages: {
    after: function (n, e) {
      return '      ' + e[0]
    },
    alpha_dash: function (n) {
      return '    ,   '
    },
    alpha_num: function (n) {
      return '      '
    },
    alpha_spaces: function (n) {
      return '      '
    },
    alpha: function (n) {
      return '    '
    },
    before: function (n, e) {
      return '      ' + e[0]
    },
    between: function (n, e) {
      return '    ' + e[0] + '  ' + e[1]
    },
    confirmed: function (n, e) {
      return '    ' + e[0]
    },
    credit_card: function (n) {
      return '     '
    },
    date_between: function (n, e) {
      return '    ' + e[0] + '  ' + e[1]
    },
    date_format: function (n, e) {
      return '     ' + e[0]
    },
    decimal: function (n, e) {
      void 0 === e && (e = [])
      var t = e[0]
      return void 0 === t && (t = '*'), '       ' + (t === '*' ? '' : t) + '  '
    },
    digits: function (n, e) {
      return '       ' + e[0] + ' '
    },
    dimensions: function (n, e) {
      return '   ' + e[0] + '   ' + e[1] + ' '
    },
    email: function (n) {
      return '     '
    },
    ext: function (n, e) {
      return '    . (' + e.slice(0) + ')'
    },
    image: function (n) {
      return '   '
    },
    in: function (n) {
      return '    '
    },
    ip: function (n) {
      return '    IP-'
    },
    max: function (n, e) {
      return '     ' + e[0] + ' '
    },
    max_value: function (n, e) {
      return '   ' + e[0] + '  '
    },
    mimes: function (n, e) {
      return '     . (' + e.slice(0) + ')'
    },
    min: function (n, e) {
      return '     ' + e[0] + ' '
    },
    min_value: function (n, e) {
      return '   ' + e[0] + '  '
    },
    not_in: function (n) {
      return '    '
    },
    numeric: function (n) {
      return '   '
    },
    regex: function (n) {
      return '   '
    },
    required: function (n) {
      return '   '
    },
    size: function (n, e) {
      return '   ,  ' + (function (n) {
        var e = (n = 1024 * Number(n)) == 0 ? 0 : Math.floor(Math.log(n) / Math.log(1024))
        return 1 * (n / Math.pow(1024, e)).toFixed(2) + ' ' + ['Byte', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'][e]
      }(e[0]))
    },
    url: function (n) {
      return '    URL'
    }
  },
  attributes: {}
}

Validator.localize('ru', dictionary)



